@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@500;700&family=Titan+One&display=swap";
*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, ul, p, figure {
  border: 0;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  font-style: normal;
  text-decoration: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  border: none;
  outline: none;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  line-height: 1.15;
}

img {
  vertical-align: middle;
  height: auto;
  max-width: 100%;
  display: block;
}

body {
  margin: 0;
  padding: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --fs-200: .5rem;
  --fs-300: .75rem;
  --fs-325: .75rem;
  --fs-350: .75rem;
  --fs-375: .875rem;
  --fs-400: .875rem;
  --fs-500: 1rem;
  --fs-550: 1.125rem;
  --fs-600: 1.25rem;
  --fs-700: 1.625rem;
  --fs-750: 1.75rem;
  --fs-800: 2.125rem;
  --fs-850: 2.25rem;
  --fs-900: 3rem;
  --ff-primary: "DM Sans", sans-serif;
  --ff-header: "Titan One", cursive;
  --bg-white: #fff;
  --bg-gray: #fafafa;
  --bg-pink: #ffb8ca;
  --bg-pink-light: #fff5f6;
  --bg-pink-dark: #e17992;
  --bg-beige: #f0d1a5;
  --bg-green: #c2e297;
  --bg-gray-hover: #ecebeb;
  --text-white: #fff;
  --text-pink: #d41443;
  --text-pink-light: #ffa5ba;
  --text-black: #000;
  --text-gray: #907e82;
  --stroke: #e1e1e1;
  --shadow: #d414431a;
  --box-shadow: 0px, 8px, 30px, var(--shadow);
  --box-radius: 1.5rem;
  --button-radius: 1.375rem;
  --pd-container: 1.25rem;
  --device-screen: 30em;
}

@media (min-width: 48em) {
  :root {
    --pd-container: 2.125rem;
    --fs-325: .875rem;
    --fs-350: 1rem;
    --fs-375: 1rem;
    --fs-550: 1.625rem;
    --fs-700: 1.375rem;
    --fs-800: 3rem;
    --fs-850: 3rem;
    --fs-900: 2.125rem;
    --device-screen: 48em;
  }
}

@media (min-width: 80em) {
  :root {
    --pd-container: 7.25rem;
    --fs-200: .875rem;
    --fs-300: .875rem;
    --fs-325: 1rem;
    --fs-375: 1.25rem;
    --fs-400: 1rem;
    --fs-550: 1.875rem;
    --fs-600: 1.875rem;
    --fs-700: 2.375rem;
    --fs-750: 2.875rem;
    --fs-800: 3.625rem;
    --fs-850: 3.625rem;
    --fs-900: 3.625rem;
    --device-screen: 80em;
  }
}

