.gallery {
  width: 100%;
  background: linear-gradient(to bottom, var(--bg-pink-light) 50%, var(--bg-white) 50%);
  margin-inline: auto;
}

.gallery__picture {
  margin-inline: auto;
}

.gallery__grid {
  --c: 7;
  --r: 6;
  --g: 4px;
  --s: 100px;
  --f: 1.5;
  width: calc(var(--c) * var(--s)  + (var(--c)  - 1) * var(--g));
  height: calc(var(--r) * var(--s) * .5 + (var(--r)  - 1) * var(--g));
  aspect-ratio: 1;
  grid-template-columns: repeat(var(--c), auto);
  grid-gap: var(--g);
  margin-inline: auto;
  display: none;
}

.gallery__grid__item {
  width: 0;
  height: 0;
  min-height: 100%;
  min-width: 100%;
  object-fit: cover;
  border-radius: 20px;
  transition: all .3s ease-in-out;
  overflow: hidden;
}

.gallery__grid__item:hover, .gallery__grid__item:focus {
  cursor: pointer;
  box-shadow: var(--box-shadow);
  filter: grayscale(0);
  width: calc(var(--s) * var(--f));
  height: calc(var(--s) * var(--f));
}

.gallery__grid__item:nth-child(1) {
  grid-row: 3 / 5;
}

.gallery__grid__item:nth-child(2) {
  grid-row: 2 / 4;
}

.gallery__grid__item:nth-child(3) {
  grid-area: 1 / 3 / 5 / 5;
}

.gallery__grid__item:nth-child(4), .gallery__grid__item:nth-child(5) {
  grid-row: 2 / 4;
}

.gallery__grid__item:nth-child(6) {
  grid-area: 3 / 7 / 5;
}

.gallery__grid__item:nth-child(7) {
  grid-row: 4 / 6;
}

.gallery__grid__item:nth-child(8) {
  grid-area: 5 / 3 / 7 / 3;
}

.gallery__grid__item:nth-child(9), .gallery__grid__item:nth-child(10) {
  grid-row: 4 / 6;
}

@media (min-width: 80em) {
  .gallery__picture {
    display: none;
  }

  .gallery__grid {
    display: grid;
  }
}

